/* Стили компонента Mеню */
.menu h1 {
  font-size: calc(28px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
}

.menu p {
  font-size: calc(12px + (26 - 14) * ((100vh - 100px) / (1600 - 300)));
  margin-bottom: 0px;
}

.menu {
  position: absolute;
  z-index: 99;
  
  padding: 0 0% 0% 0%;
  margin: 10px;

  text-align: center;

  width: 25%;
  min-width: 260px;

  background-color: white;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgba(178, 171, 169, 1);
}

/* Стили элемента Подложка */
.outer-div {
  padding: 2%;

  background-color: white;

  border-radius: 5px;
}


.output-div {
  margin: 10px auto;
  padding: 2% 2% 2% 2%;

  max-width: 700px;

  line-height: 1.2;

  background-color: white;
  border: 1px rgb(231, 224, 227) solid;
  border-radius: 5px;
}

/* Стили элемента Поиск */
.search { 
  margin: 1% auto; 
  display: flex;
}

.search .stretch { 
  flex: 1; 
}

.search .normal { 
  flex: 0; 
  margin: 0 0 0 1rem;
}

.search div input { 
  padding: .5em 1em; 
  width: 100%; 
}

.search div button { 
  padding: .5em 1em;
  width: 100%;
  white-space: nowrap; 
}


/* Стили элемента Выбор по округам */
.custom-select {
  position: relative;
  display: block;
  margin: 1% auto;
  max-width: 500px;
  min-width: 10px;
}

.custom-select  select {
  border: 1px solid $select-color;
  outline:none;
  background: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
  margin:0;
  display:block;
  width: 100%;
  cursor: pointer;
    
  padding: 12px 55px 15px 15px;
  font-size: 14px;
}

.custom-select  &:after{
  position: absolute;
  right:0; top:0;
  width: 50px; 
  height:100%;
  line-height: 50px;
  content: '\25BC';
  text-align: center;
  z-index:-1;
  background: $select-color;
  color:#fff;
  font-size: 18px;
}

