body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Map {
	position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
}
/* Стили компонента Mеню */
.menu h1 {
  font-size: calc(28px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
}

.menu p {
  font-size: calc(12px + (26 - 14) * ((100vh - 100px) / (1600 - 300)));
  margin-bottom: 0px;
}

.menu {
  position: absolute;
  z-index: 99;
  
  padding: 0 0% 0% 0%;
  margin: 10px;

  text-align: center;

  width: 25%;
  min-width: 260px;

  background-color: white;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgba(178, 171, 169, 1);
}

/* Стили элемента Подложка */
.outer-div {
  padding: 2%;

  background-color: white;

  border-radius: 5px;
}


.output-div {
  margin: 10px auto;
  padding: 2% 2% 2% 2%;

  max-width: 700px;

  line-height: 1.2;

  background-color: white;
  border: 1px rgb(231, 224, 227) solid;
  border-radius: 5px;
}

/* Стили элемента Поиск */
.search { 
  margin: 1% auto; 
  display: flex;
}

.search .stretch { 
  flex: 1 1; 
}

.search .normal { 
  flex: 0 1; 
  margin: 0 0 0 1rem;
}

.search div input { 
  padding: .5em 1em; 
  width: 100%; 
}

.search div button { 
  padding: .5em 1em;
  width: 100%;
  white-space: nowrap; 
}


/* Стили элемента Выбор по округам */
.custom-select {
  position: relative;
  display: block;
  margin: 1% auto;
  max-width: 500px;
  min-width: 10px;
}

.custom-select  select {
  border: 1px solid $select-color;
  outline:none;
  background: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
  margin:0;
  display:block;
  width: 100%;
  cursor: pointer;
    
  padding: 12px 55px 15px 15px;
  font-size: 14px;
}

.custom-select  &:after{
  position: absolute;
  right:0; top:0;
  width: 50px; 
  height:100%;
  line-height: 50px;
  content: '\25BC';
  text-align: center;
  z-index:-1;
  background: $select-color;
  color:#fff;
  font-size: 18px;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

